import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    dataRules: [],
    activeRulesItem: {}
}

export const rulesSlice = createSlice({
  name: 'rules',
  initialState,
  reducers: {
    setDataRules: (state, action) => {
      state.dataRules = action.payload
    },
    setActiveRulesItem: (state, action) => {
        state.activeRulesItem = action.payload
      },
  },
})


export const { setDataRules, setActiveRulesItem } = rulesSlice.actions

export default rulesSlice.reducer