import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";

function SingleRules() {
  const [synth, setSynth] = useState(null);
  const { activeRulesItem } = useSelector((state) => state.rules);

  //озвучення
  useEffect(() => {
    setSynth(window.speechSynthesis);
  }, []);

  const speak = (text) => {
    const utterance = new SpeechSynthesisUtterance(text);
    synth.speak(utterance);
  };

  /* end озвучення */

  return (
    <div>
      <h1>{activeRulesItem.name}</h1>

      {activeRulesItem.img &&  <div className="img">
        {activeRulesItem.img.map((img, index) => (
            <img src={process.env.PUBLIC_URL + `/img/${img}.jpg`} alt="img" key={index}/>
          ))}
      </div>}
     

      <p
        className="voice"
        onClick={() => speak("I study French. – Я вчу французьку мову.")}
      >
        I study French. – Я вчу французьку мову.
      </p>
    </div>
  );
}

export default SingleRules;
