import React, { useState, useEffect } from "react";
import { setDataRules, setActiveRulesItem } from "../redux/slices/rulesSlice";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { Link } from "react-router-dom";

function Information() {
  const { dataRules } = useSelector((state) => state.rules);
  const dispatch = useDispatch();

  useEffect(() => {
    axios

      .get(`https://api.jsonbin.io/v3/b/6442baf8ebd26539d0aff4ad`)
      .then((arr) => {
        dispatch(setDataRules(arr.data.record.rules));
      });
  }, []);

  return (
    <div>
      <div className="items">
        {dataRules &&
          dataRules.map((item) => (
            <div className="card" key={item.id}>
              <img
                src={process.env.PUBLIC_URL + `/img/${item.img[0]}.jpg`}
                alt="Зображення"
              />
              <div className="container">
                <h4>
                  <b>{item.name}</b>
                </h4>
                <Link
                  to="/singlerules"
                  onClick={() => dispatch(setActiveRulesItem(item))}
                >
                  Читати
                </Link>
              </div>
            </div>
          ))}
      </div>
    </div>
  );
}

export default Information;
